  position: fixed;
  visibility: hidden;
  display: inline-block;
  right: 20px;
  bottom: 20px;
  padding: 8px 10px;
  border-radius: 7px;
  border: 2px solid lightseagreen;
  background-color: whitesmoke;
  color: lightseagreen;
  opacity: 0;
  transition: all 1000ms;
  -webkit-transform: scale(0.8);
          transform: scale(0.8); }
  .btt i {
    font-size: 2em; }
  @media all and (min-width: 768px) {
    .btt {
      -webkit-transform: scale(1);
              transform: scale(1); } }

.btt-visible {
  visibility: visible;
  opacity: 0.5; }

.btt-visible:hover {
  opacity: 1; }

footer {
  text-align: center; }

.loader {
  text-align: center;
