::-webkit-input-placeholder {
  color: #4bc9c9;
  font-weight: 300; }

::-ms-input-placeholder {
  color: #4bc9c9;
  font-weight: 300; }

::placeholder {
  color: #4bc9c9;
  font-weight: 300; }

.page-buttons-container {
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.page-button {
  display: inline-block;
  text-align: center;
  padding: 3px 8px;
  border: 1px solid lightseagreen;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
