  -webkit-justify-content: space-around;
          justify-content: space-around; }

h1 {
  margin: 0; }

/* Nav */
nav {
  margin: 0; }

nav ul {
  display: none;
  list-style: none;
  padding: 0;
  margin: 0; }

.nav-visible {
  display: block; }

nav li {
  margin: 0;
  padding: 0;
