  width: 25px;
  height: 25px;
  color: white;
  background-color: black;
  text-align: center;
  font-size: 20px;
  line-height: 25px;
  position: relative;
  top: 5px;
  right: 5px;
  float: right; }

.mask {
  background: black;
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  z-index: 1;
  cursor: pointer; }

.app-mask {
  cursor: auto;
